.Header {
  height: 65px;
  position: absolute;
  overflow: hidden;
  width: 100%;
  max-width: 1400px;
  margin: 0px auto;
}

.AllNavigation {
  position: fixed;
  display: flex;
  z-index: 1;
  top: 20px;
  right: 0;
}

.burger {
  display: flex;
}

.logo {
  font-size: 18px;
  color: floralwhite;
  font-weight: 1000;
  font-family: "Mulish";
  margin: 23px;
  cursor: pointer;
  z-index: 1;
  position: absolute;
}

.Navigation {
  color: #d6d6d6;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  display: flex;
  font-size: 20px;
  transition: 0.4s;
}
.Navigation .NavOne,
.Navigation .NavTwo {
  transition: 0.4s;
}
.Navigation .NavOne:hover {
  color: white;
}
.Navigation .NavTwo:hover {
  color: white;
}

.Contacts {
  align-items: center;
}

.Inst {
  width: 27px;
  cursor: pointer;
  margin-right: 20px;
}

.Tg {
  width: 26px;
  margin-right: 30px;
  cursor: pointer;
}

.WhtsApp {
  width: 27px;
  margin-right: 20px;
  cursor: pointer;
}

.openHabmurger {
  cursor: pointer;
  outline: none;
  border: none;
  background-color: transparent;
  color: white;
  visibility: hidden;
  font-size: 20px;
}

@media (max-width: 550px) {
  .openHabmurger {
    visibility: visible;
    margin-right: 1em;
    z-index: 1;
  }
  .Navigation {
    font-size: 25px;
  }
  .Contacts {
    margin-top: 2em;
  }
  .burger {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: -100%;
    right: 0;
    height: 100vh;
    width: 100%;
    transition: left 0.5s;
    border-radius: 10px;
    background: rgb(0, 0, 0);
    background: linear-gradient(327deg, rgb(0, 0, 0) 0%, rgb(13, 52, 74) 40%, rgb(37, 40, 54) 100%);
  }
  .active {
    left: 0;
  }
}/*# sourceMappingURL=Header.module.css.map */